.contact__container {
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
}


.contact__title {
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.contact__info {
    display: grid;
    row-gap: 2rem;
    grid-template-columns: 300px;
}

.contact__card {
    background-color: var(--color-background);
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1rem;
    text-align: center;
}

.contact__card-icon {
    font-size: 1.2rem;
    margin-bottom: var(--mb-0-25);
    color: var(--color-on-background-secondary);
}

.contact__card-title,
.contact__card-data {
    font-size: var(--small-font-size);
    color: var(--color-on-background-secondary);
}

.contact__card-title {
    font-weight: var(--font-medium);
}

.contact__card-data {
    display: block;
    margin-bottom: var(--mb-0-75);
}

.contact__button {
    color: var(--color-primary);
    font-size: var(--small-font-size);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    column-gap: 0.25rem;
    text-align: center;
}

.contact__button-icon {
    font-size: 1rem;
    transition: 0.3s;
}

.contact__button:hover .contact__button-icon {
    transform: translate(0.25rem);
}

.contact__form {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.contact__form-div {
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact__form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid var(--color-on-background-secondary);
    background: none;
    color: var(--color-on-background);
    padding: 1.5rem;
    z-index: 1;
}

.contact__form-area {
    height: 11rem;
}

.contact__form-area textarea {
    resize: none;
}

.contact__form-tag {
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--color-background-secondary);
    z-index: 10;
    color: var(--color-primary);
}

.contact__form input:focus,
.contact__form textarea:focus {
    border-color: var(--color-primary);
    border-color: var(--color-primary);
}


/* Media Queries */
/* For large devices */

@media screen and (max-width: 992px) {
    .contact__container {
        column-gap: 3rem;
    }
}

/* For medium devices */

@media screen and (max-width: 768px) {
    .contact__container {
        grid-template-columns: 1fr;
        row-gap: 3rem;
    }

    .contact__info {
        justify-content: center;
    }

    .contact__form {
        margin: 0 auto;
    }
}

@media screen and (max-width: 576px) {
    .contact__info {
        grid-template-columns: 1fr;
        padding: 1rem 0;
    }

    .contact__form {
        width: 100%;
    }
}

/* For small devices */

@media screen and (max-width: 350px) {}